@font-face {
  font-family: "Montserrat-Regular";
  src: url("../fonts/Montserrat-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat-Medium";
  src: url("../fonts/Montserrat-Medium.woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat-SemiBold";
  src: url("../fonts/Montserrat-SemiBold.woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat-ExtraBold";
  src: url("../fonts/Montserrat-ExtraBold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat-Black";
  src: url("../fonts/Montserrat-Black.woff2");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Montserrat-Regular", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #333333;
}
body.lock {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1330px;
  padding: 0 15px;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: inherit;
}

.link-reset {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.title {
  margin: 0;
  margin-bottom: 60px;
  font-family: "Montserrat-ExtraBold", sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 50px;
  text-transform: uppercase;
}

.text {
  margin: 0;
}

.button {
  padding: 8px 83px;
  font-family: "Montserrat-ExtraBold", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #FFFFFF;
  background-color: #547147;
  border: 2px solid #547147;
  border-radius: 40px;
  transition: background-color 0.4s;
}
.button:hover {
  background-color: transparent;
  color: #547147;
}

.inner-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: #547147;
  border-radius: 100px;
  transition: transform 0.4s;
}
.inner-icon:hover {
  transform: scale(0.9);
}

.inner-icon {
  margin-right: 20px;
}
.inner-icon:last-child {
  margin-right: 0;
}

.social-icon {
  width: 24px;
  height: 24px;
  fill: #FFFFFF;
}

.input {
  all: unset;
  margin-bottom: 20px;
  padding: 20px 15px;
  border: 1px solid #C2C2C2;
  border-radius: 10px;
  width: 100%;
  box-sizing: border-box;
  cursor: text;
}
@media (max-width: 768px) {
  .input {
    padding: 15px;
    font-size: 14px;
    line-height: 18px;
  }
}

