.header {
  z-index: 10;
  padding: 15px 0;
}
.header__container {
  position: relative;
}
.header__container::after {
  position: absolute;
  content: "";
  border-top: 1px solid #547147;
  width: 100%;
  bottom: -15px;
  left: 0;
}
.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 5;
}
.header__phone {
  margin-right: 30px;
  font-family: "Montserrat-SemiBold", sans-serif;
  font-weight: 600;
  position: relative;
}
.header__phone::after {
  position: absolute;
  content: "";
  width: 0;
  border-top: 2px solid #333333;
  left: 50%;
  top: 105%;
  transform: translateX(-50%);
  transition: width 0.4s;
}
.header__phone:hover::after {
  width: 100%;
}
.header__inner-icon {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  position: relative;
  width: 200px;
  height: 65px;
  z-index: 5;
}
.logo--footer {
  margin-bottom: 30px;
  width: 264px;
  height: 84px;
}
.logo__img {
  width: 100%;
  height: 100%;
}
.logo__img-adaptive {
  display: none;
}

.burger {
  display: none;
}

.menu__item {
  position: relative;
}
.menu__item::after {
  position: absolute;
  content: "";
  width: 0;
  border-top: 2px solid #404936;
  top: 100%;
  right: 0;
  transition: all 0.4s;
}
.menu__item:hover::after {
  width: 100%;
  left: 0;
}
.menu__link {
  position: relative;
}

/*-------------------------*/
body.touch .menu__link {
  flex: 1 1 auto;
  transition: transform 0.4s ease 0s;
}

/*-------------------------*/
@media screen and (min-width: 993px) {
  .menu {
    display: flex;
    align-items: center;
  }
  .menu__item {
    margin-right: 40px;
    font-family: "Montserrat-SemiBold", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
  }
  .menu__item:last-child {
    margin-right: 0;
  }
}
@media screen and (max-width: 993px) {
  .header {
    padding: 10px 0;
  }
  .header__container::after {
    bottom: -10px;
  }
  .header__phone {
    margin-right: 20px;
    color: #404936;
  }
  .header__inner-icon {
    display: none;
  }
  .logo {
    width: 41px;
    height: 43px;
  }
  .logo--footer {
    width: 264px;
    height: 84px;
  }
  .logo__img {
    display: none;
  }
  .logo__img--footer {
    display: block;
  }
  .logo__img-adaptive {
    display: block;
    width: 100%;
    height: 100%;
  }
  .burger {
    z-index: 5;
    display: block;
    width: 30px;
    height: 30px;
    background-color: #547147;
    border-radius: 50%;
    position: relative;
    cursor: pointer;
  }
  .burger::before, .burger::after, .burger__line {
    position: absolute;
    left: 50%;
    border-top: 1px solid #FFFFFF;
    width: 16px;
    background-color: #FFFFFF;
    transform: translateX(-50%);
    transition: all 0.4s ease 0s;
  }
  .burger::before, .burger::after {
    content: "";
  }
  .burger::before {
    top: 10px;
  }
  .burger::after {
    bottom: 10px;
  }
  .burger__line {
    width: 12px;
    top: 50%;
    transform: scale(1) translate(-35%, -50%);
  }
  .burger.active::before {
    top: 50%;
    left: 25%;
    transform: rotate(-45deg) translate(0, -50%);
  }
  .burger.active::after {
    bottom: 50%;
    left: 25%;
    transform: rotate(45deg) translate(0, 50%);
  }
  .burger.active .burger__line {
    transform: scale(0) translate(0, -50%);
  }
  .nav {
    display: flex;
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0);
    padding: 73px 30px 30px 30px;
    transition: left 0.4s ease 0s;
    overflow: auto;
    z-index: 2;
  }
  .nav::before {
    content: "";
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 63px;
    background-color: #FFFFFF;
    z-index: 2;
    transition: left 0.4s ease 0s;
  }
  .nav.active {
    left: 0;
  }
  .nav.active::before {
    left: 0;
  }
  .menu {
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .menu__item {
    margin-bottom: 40px;
    font-size: 24px;
    color: #FFFFFF;
  }
  .menu__item:last-child {
    margin-bottom: 0;
  }
}
